import {IDrawable, Context, Rectangle} from "../Drawing/Graphics";
import { Mouse } from "../Input/input.module";
import { IUpdateable} from "../IUpdateable";

export class StandardCursor extends Rectangle implements IDrawable, IUpdateable{
    
    constructor(x : number, y : number, width : number, height : number){
        super(x,y,width,height);
    }

    public draw(context : Context) : void{
        context.drawFilledRectangle(this, "#f00");
    }

    public update() : void{
        this.middlePoint = Mouse.mousePosition;
    }
}